const ExpandableComponent = ({ member, policyId }) => {
  const [expanded, setExpanded] = useState(false);
  return (
    <Panel
      key={member.id}
      header={
        <MemberIconWithName
          member={member}
          setExpanded={setExpanded}
          expanded={expanded}
          policyId={policyId}
        />
      }
      expanded={expanded}
      id={"panel" + member.id}
      defaultexpanded="false"
    >
      {expanded ? "" : null}
    </Panel>
  );
};

